import React from 'react';
import { useHistory } from 'react-router-dom';
import { Routes } from '../../pages/routes';

export type HeaderProps = {};

export const HeaderComponent: React.FC<HeaderProps> = React.memo((props: HeaderProps) => {
    const history = useHistory();

    const handleRedirectTo = React.useCallback(
        (path: string) => {
            history.push(path);
        },
        [history]
    );

    return (
        <>
            Header Component
            <nav>
                <ul>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.home)}>Home</div>
                    </li>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.group)}>Group</div>
                    </li>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.label)}>Label</div>
                    </li>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.expense)}>Expense</div>
                    </li>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.settings)}>Settings</div>
                    </li>
                    <li>
                        <div onClick={() => handleRedirectTo(Routes.auth)}>Auth</div>
                    </li>
                </ul>
            </nav>
        </>
    );
});
